
.select-main {
    width: 100%;
}

.select-close {
    float: none;
    line-height: 12px;
    vertical-align: initial;
}

.select-button {
    width: 100%;
}

.select-button .select-info {
    position: absolute;
    top: 6px;
    right: 36px;
}

.select-button .select-close {
    position: absolute;
    top: 8px;
    right: 20px;
}
.select-button::after {
    position: absolute;
    top: 14px;
    right: 8px;
}

.select-name {
    width: 100%;
    padding-right: 46px;
    text-align: left;
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    vertical-align: bottom;
}
.select-name-item{
    padding-right: 4px;
}

.select-input{
    width: 100%;
    position: relative;
    display: inline-block;
    font-weight: normal;
    line-height: 1.42857143;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    border: 1px solid transparent;
    padding: 0.25rem 0.75rem;
    font-size: 0.875rem;
    border-radius: 0.125rem;
    color: #373a3c;
    background-color: #fff;
    border-color: #ccc;
    .select-name{
        padding-right: 0px;
        padding-right: 16px;
        .select-name-cursor{
            cursor: text;
            border: none;
            height: 20px;
        }
        .select-name-item{
            background-color: #f3f3f3;
            border-radius: 4px;
            float: left;
            overflow: hidden;
            -webkit-transition: padding 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
            transition: padding 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
            padding: 2px 10px 2px 8px;
            font-size: 8px;
            font-weight: lighter;
            line-height: 14px;
            margin-left: 2px;
            .fa:before{
                line-height: 8px;
            }
        }
    }
    .select-close{
        position: absolute;
        top: 8px;
        right: 8px;
    }
}
.search-box {
    width: 100%;
    border: none;
    padding: 0px 10px;
    outline: none;
}

.dropdown-select {
    display: none;
}
.dropdown-searchbox {
    position: relative;
    border-bottom: 1px solid #ccc;
}
.dropdown-scroll{
    max-height: 400px;
    overflow-y: scroll;
}
.dropdown-loadmore{
    position: relative;
    text-align: center;
    cursor: pointer;
}
.dropdown-loadmore-link.btn.focus, .dropdown-loadmore-link.btn:focus, .dropdown-loadmore-link.btn:active:focus{
    width: 100%;
    outline: none;
}
.dropdown-loadmore-link{
    width: 100%;
    outline: none;
}
.search-close{
    position: absolute;
    top: 0px;
    right: 8px;
}

/* 必需 */
.expand-transition {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
/* .expand-enter 定义进入的开始状态 */
/* .expand-leave 定义离开的结束状态 */
.expand-enter, .expand-leave {
  opacity: 0;
}

.spinner-loading-icon {
    -webkit-animation: antRotate 3.2s infinite linear;
            animation: antRotate 3.2s infinite linear;
}
@-webkit-keyframes antRotate {
  to {
    -webkit-transform: rotate(405deg);
            transform: rotate(405deg);
  }
}
@keyframes antRotate {
  to {
    -webkit-transform: rotate(405deg);
            transform: rotate(405deg);
  }
}



