.webuploader-element-invisible{position:absolute !important;clip:rect(1px 1px 1px 1px);clip:rect(1px, 1px, 1px, 1px)}.webuploader-container{display:inline-block}.webuploader-pick{width:100%;height:100%}.uploader{border:1px solid #d9d9d9;width:96px;height:96px;border-radius:2px;background-color:#fbfbfb;display:inline-block;margin-right:8px;margin-bottom:8px;padding:8px;position:relative;vertical-align:top}.uploader .rounded{width:100%}.uploader .uploader-operator{position:absolute;background:#333;color:#fff;width:78px;bottom:8px;opacity:.9;display:none;padding:0 4px}.uploader .uploader-operator .fa{cursor:pointer;opacity:10;float:right}.uploader .uploader-operator .thumb-name{display:inline-block;overflow:hidden;width:30px;font-size:8px;line-height:8px;padding:6px 0}.uploader:hover .uploader-operator{display:block}.uploader-select{border:1px dashed #d9d9d9;cursor:pointer;-webkit-transition:border-color .3s ease;transition:border-color .3s ease;padding:36px 0;text-align:center}.uploader-select:hover{border-color:#108ee9}.uploader-btn{position:relative;height:20px}.uploader-list{list-style:none;margin:0;padding:0;margin-top:8px}.uploader-list .uploader-item{padding:8px 8px 0 8px}.thumb-remove{cursor:pointer}


